import styles from "./Applicant.module.css";
import PhoneInput from "react-phone-input-2";
import { RefereeDetailsStep2 } from "./RefereeDetailsStep2";
import { TextField, Grid, Button, IconButton } from "@mui/material";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { useState } from "react";
import { changeShade } from "../../helpers";
import { Colors } from "../../config";

export const RefereeDetails = ({ move }) => {
  const [step, setStep] = useState(false);
  const [date, setDate] = useState(null);
  return (
    <>
      <div>
        <p
          style={{
            fontSize: "40px",
            margin: "0",
            fontFamily: "Roboto Black",
          }}
        >
          Reference request from Opentech
        </p>
      </div>
      {step ? (
        <RefereeDetailsStep2 move={move} />
      ) : (
        <div className={styles.detailsContainer}>
          <p style={{ fontSize: "18px", fontWeight: "500" }}>
            Please enter reference details
          </p>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <TextField
                select
                variant="outlined"
                fullWidth
                InputProps={{
                  style: {
                    borderRadius: "8px",
                  },
                }}
                label="This referee is/was my"
                style={{ opacity: "47%" }}
              />
            </Grid>
            <Grid item xs={6}>
              <TextField
                variant="outlined"
                fullWidth
                InputProps={{
                  style: {
                    borderRadius: "8px",
                  },
                }}
                placeholder="Referee's Email Address"
                style={{ opacity: "47%" }}
              />
            </Grid>
            <Grid item xs={6}>
              <TextField
                variant="outlined"
                fullWidth
                InputProps={{
                  style: {
                    borderRadius: "8px",
                  },
                }}
                placeholder="Referee's Full Name"
                style={{ opacity: "47%" }}
              />
            </Grid>
            <Grid item xs={6}>
              <PhoneInput
                placeholder="Referee's Phone"
                fullWidth
                country={"gb"}
                dropdownStyle={{ textAlign: "left" }}
                specialLabel=""
                inputStyle={{
                  borderRadius: "8px",
                  width: "100%",
                  opacity: "47%",
                }}
              />
            </Grid>
            <Grid item container xs={12} sx={{ marginTop: "30px" }} spacing={2}>
              <Grid item xs={6}>
                <TextField
                  variant="outlined"
                  fullWidth
                  InputProps={{
                    style: {
                      borderRadius: "8px",
                    },
                  }}
                  placeholder="Company Name"
                  style={{ opacity: "47%" }}
                />
              </Grid>
              <Grid item xs={6}>
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                  <DatePicker
                    InputProps={{
                      style: {
                        width: "100%",
                        borderRadius: "8px",
                      },
                      disableUnderline: true,
                    }}
                    label="Start Date"
                    value={date}
                    onChange={newValue => {
                      setDate(newValue);
                    }}
                    renderInput={params => (
                      <TextField
                        fullWidth
                        variant="outlined"
                        style={{ opacity: "47%" }}
                        {...params}
                      />
                    )}
                    style={{ opacity: "47%" }}
                  ></DatePicker>
                </LocalizationProvider>
              </Grid>
              <Grid item xs={6}>
                <TextField
                  variant="outlined"
                  fullWidth
                  InputProps={{
                    style: {
                      borderRadius: "8px",
                    },
                  }}
                  placeholder="Job Title"
                  style={{ opacity: "47%" }}
                />
              </Grid>
              <Grid item xs={6}>
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                  <DatePicker
                    InputProps={{
                      style: {
                        width: "100%",
                        borderRadius: "8px",
                      },
                      disableUnderline: true,
                    }}
                    label="End Date"
                    value={date}
                    onChange={newValue => {
                      setDate(newValue);
                    }}
                    renderInput={params => (
                      <TextField
                        fullWidth
                        variant="outlined"
                        style={{ opacity: "47%" }}
                        {...params}
                      />
                    )}
                    style={{ opacity: "47%" }}
                  ></DatePicker>
                </LocalizationProvider>
              </Grid>
              <Grid item xs={6}>
                <TextField
                  variant="outlined"
                  fullWidth
                  InputProps={{
                    style: {
                      borderRadius: "8px",
                    },
                  }}
                  placeholder="Company Address"
                  style={{ opacity: "47%" }}
                  multiline
                  rows={3}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  variant="outlined"
                  fullWidth
                  InputProps={{
                    style: {
                      borderRadius: "8px",
                      alignItems: "baseline",
                    },
                    endAdornment: (
                      <IconButton sx={{ fontSize: "12px", color: "#1E1F21" }}>
                        {" "}
                        Clear Signature
                      </IconButton>
                    ),
                  }}
                  placeholder="Signature"
                  style={{ opacity: "47%" }}
                  multiline
                  rows={3}
                ></TextField>
              </Grid>
            </Grid>
            <Grid item xs={6}>
              <Button
                onClick={() => {
                  setStep(true);
                }}
                sx={{
                  backgroundColor: Colors.primary,
                  color: Colors.trueWhite,
                  width: "100%",
                  height: "60px",
                  borderRadius: "6px",
                  marginTop: "10px",
                  textTransform: "unset",
                  fontSize: "22px",
                  fontWeight: "bold",
                  "&:hover": {
                    backgroundColor: changeShade(Colors.primary, -10),
                  },
                }}
              >
                <strong>Save Reference</strong>
              </Button>
            </Grid>
          </Grid>
        </div>
      )}
    </>
  );
};
